.banner {
  background-color: #F4EFE9;
  border-radius: 20px;
  padding: 88px 0 52px 61px;
  margin-top: 45px;
  position: relative;
  overflow: hidden;
}

.before {
  position: absolute;
  top: 5px;
  background-size: contain;
  background-repeat: no-repeat;
  width: 100px;
  height: 100px;
  left: 15px;

}

.after {
  position: absolute;
  top: 0;
  background-repeat: no-repeat;
  right: 0;
  bottom: 0;
  // width: 38vw;
  left: 38%;
  background-size: cover;
  z-index: 0;


}

.title {
  font-size: 38px;
  line-height: 46px;
  margin-bottom: 10px;
  z-index: 2 !important;
  position: relative;
  font-weight: 700;
}

.span {
  color: #8BB43C;
}

.button {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  text-align: center;
  background: #A5D364;
  border-radius: 110px;
  padding: 15px 52px;
  text-transform: uppercase;
  position: relative;
  z-index: 2;
  border: none;
}

@media (max-width : 980px) {
  .after {
    right: -200px;
    width: 100%;
  }
}

@media (max-width : 630px) {
  .after {
    right: -82%;
    width: 137%;
    bottom: 0;
  }

  .title {
    font-size: 29px;
    line-height: 29px;
  }

  .banner {
    padding: 47px 0 28px 23px;
    margin-top: 25px;
  }

  .button {
    font-size: 14px;
    line-height: 19px;
    padding: 9px 34px;
  }
}